import React, { Component } from 'react';
import './NearestMasjid.css';
import Grid from 'material-ui/Grid';
import { withStyles } from 'material-ui/styles';
import Paper from 'material-ui/Paper';
import Typography from 'material-ui/Typography';
import { LinearProgress } from 'material-ui/Progress';
import List, { ListItem, ListItemText,ListSubheader} from 'material-ui/List';
import Avatar from 'material-ui/Avatar';
import Icon from 'material-ui/Icon';
import Card, { CardHeader, CardMedia, CardContent, CardActions } from 'material-ui/Card';
import IconButton from 'material-ui/IconButton';
import MoreVertIcon from 'material-ui-icons/MoreVert';
import FavoriteIcon from 'material-ui-icons/Favorite';
import ShareIcon from 'material-ui-icons/Share';

const styles = theme => ({
  root: theme.mixins.gutters({
    paddingTop: 16,
    paddingBottom: 16,
    marginTop: theme.spacing.unit * 1,
  }),
  marginTop:{
  	marginTop: theme.spacing.unit * 1,
  },
  avatar: {
    backgroundColor: 'red',
  },
  media: {
    height: 194,
  },
  actions: {
    display: 'flex',
  },
});
class NearestMasjid extends Component{
	constructor(props){
		super(props);
		this.state = {
	      error: null,
	      isLoaded: false,
	      prayertimesdata: false,
	      prayerlocationdata: false,
	    };
	    console.log(this.props.geolocation)
	}
	componentDidMount() {
		console.log(this.props.geolocation)
		
	}
	fetchPrayerTimedata=(geolocation)=>{
		fetch(`http://api.aladhan.com/v1/timings/${Math.round((new Date()).getTime() / 1000)}?latitude=${geolocation.latitude}&longitude=${geolocation.longitude}&method=2`)
	      .then(res => res.json())
	      .then(
	        (result) => {
	        	console.log(result.data.timings);
	        	this.setState({
		            prayertimesdata:result.data.timings
		        });
	        },
	        // Note: it's important to handle errors here
	        // instead of a catch() block so that we don't swallow
	        // exceptions from actual bugs in components.
	        (error) => {
	          this.setState({
	            isLoaded: true,
	            error
	          });
	        }
	      )
	}
	fetchNearestPrayerLocation=(geolocation)=>{
		var request = {
		location: {lat: geolocation.latitude, lng: geolocation.longitude},
	    radius: '500',
	    type: ['mosque']
	  };
		let service = new window.google.maps.places.PlacesService(document.createElement('div'));
  		service.nearbySearch(request, (results, status)=>{
		  console.log(results, status);
		  	this.setState({
	            prayerlocationdata:results
	        });
		});

	}
	componentDidUpdate(prevProps, prevState, snapshot){
		const {geolocation} = this.props;
		if(geolocation.latitude != prevProps.geolocation.latitude){
			this.fetchPrayerTimedata(geolocation);
			this.fetchNearestPrayerLocation(geolocation);
		}
	}
	ifPlaceHasPhotos=(locationdata)=>{
		const { classes} = this.props;
		if (locationdata.hasOwnProperty('photos')) {
			const photo=locationdata.photos[0];
		  return (
		  	<CardMedia
			    className={classes.media}
			    image={photo.getUrl({'maxWidth': 345, 'maxHeight': 194})}
			    title={locationdata.name}
			  />
		  )
		}
	}
	renderPrayerLocations=()=>{
		const { classes,geolocation} = this.props;
		const {prayerlocationdata}=this.state;
		if(prayerlocationdata){
			const renderui=prayerlocationdata.map((locationdata)=>{
				return (
					<Grid key={locationdata.place_id} item xs={12} sm={4}>
					<Card>
					  <CardHeader
					    avatar={
					      <Avatar aria-label="Recipe" className={classes.avatar}>
					        R
					      </Avatar>
					    }
					    action={
					      <IconButton>
					        <MoreVertIcon />
					      </IconButton>
					    }
					    title={locationdata.name}
					    subheader="September 14, 2016"
					  />
					  {this.ifPlaceHasPhotos(locationdata)}
					  
					  <CardContent>
					    <Typography component="p">
					      {locationdata.vicinity}

					    </Typography>
					  </CardContent>
					  <CardActions className={classes.actions} disableActionSpacing>
					    <IconButton aria-label="Add to favorites">
					      <FavoriteIcon />
					    </IconButton>
					    <IconButton aria-label="Share">
					      <ShareIcon />
					    </IconButton>
					  </CardActions>
					</Card>
				</Grid>
				)
			})
			return (
				<div className={classes.marginTop}>
				<Grid container spacing={24}>
					{renderui}
				</Grid>
				</div>
			)
		}else{
			return(
				<Paper className={classes.root} elevation={4}>
			        <Typography variant="headline" component="h3">
			          This is a sheet of paper. {this.props.currentLocation}
			        </Typography>
			        <LinearProgress />
			      </Paper>
			)
		}
	}
	renderPrayerTimedata=()=>{
		const { classes,geolocation} = this.props;
		const {isLoaded,prayertimesdata}=this.state;
		if(prayertimesdata){
			const renderui = Object.keys(prayertimesdata).map((key)=>{
			    return (
			    	<ListItem key={key}>
			          <Avatar>
			          <Icon>
				        access_time
				      </Icon>
			          </Avatar>
			          <ListItemText primary={key} secondary={prayertimesdata[key]} />
			        </ListItem>
			    )
			});
        	return (
        		<Paper className={classes.root} elevation={4}>
			        <List
			        	subheader={<ListSubheader component="div">Nested List Items</ListSubheader>}
			        >
			        {renderui}
			      </List>
		      </Paper>
      		)
		}else{
		return (
				<Paper className={classes.root} elevation={4}>
			        <Typography variant="headline" component="h3">
			          This is a sheet of paper. {this.props.currentLocation}
			        </Typography>
			       <LinearProgress />
			     </Paper>
			)	
		}
		
	}
	render(){
		const { classes,geolocation} = this.props;
		const {isLoaded,prayertimesdata}=this.state;
		return(
			<div>
			<Grid container spacing={24}>
			<Grid item xs={12} sm={9}>
				{this.renderPrayerLocations()}
			</Grid>
			<Grid item xs={12} sm={3}>
				{this.renderPrayerTimedata()}
			</Grid>
			</Grid>
			</div>
		)
	}
}
export default withStyles(styles)(NearestMasjid);



