import React, { Component } from 'react';
import './PrayerRoot.css';

import LocationSearch from '../components/LocationSearch/LocationSearch';

import PrayerTime from '../components/PrayerTime/PrayerTime';
import NearestMasjid from '../components/NearestMasjid/NearestMasjid';

import { withStyles } from 'material-ui/styles';
import Drawer from 'material-ui/Drawer';
import AppBar from 'material-ui/AppBar';
import Toolbar from 'material-ui/Toolbar';
import List, { ListItem, ListItemIcon, ListItemText } from 'material-ui/List';
import Typography from 'material-ui/Typography';
import IconButton from 'material-ui/IconButton';
import Hidden from 'material-ui/Hidden';
import Divider from 'material-ui/Divider';
import MenuIcon from 'material-ui-icons/Menu';
import InboxIcon from 'material-ui-icons/Inbox';
import DraftsIcon from 'material-ui-icons/Drafts';


const drawerWidth = 240;

const styles = theme => ({
  root: {
    flexGrow: 1,
    zIndex: 1,
    overflow: 'hidden',
    position: 'relative',
    display: 'flex',
    width: '100%',
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
  },
  navIconHide: {
    [theme.breakpoints.up('md')]: {
      display: 'none',
    },
  },
  toolbar: theme.mixins.toolbar,
  drawerPaper: {
    width: drawerWidth,
    [theme.breakpoints.up('md')]: {
      position: 'relative',
    },
  },
  content: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.default,
    padding: theme.spacing.unit * 1,
  },
  active:{
  	backgroundColor: 'rgba(0, 0, 0, 0.08)'
  }
});

class PrayerRoot extends Component {
	constructor(props){
		super(props);
		this.state={
			mobileOpen: false,
			activepage:'NearestMasjid',
			currentlocation:'Dhaka',
			geolocation:false,
		}
	}
	handleDrawerToggle = () => {
	    this.setState({ mobileOpen: !this.state.mobileOpen });
	}
	handleLocationChange=(locationinformation)=>{
		this.setState({currentlocation:locationinformation});
	}
	handleGeolocationChange=(locationinformation)=>{
		console.log(locationinformation);
		this.setState({geolocation:locationinformation});
	}
	changeCurrentPage=(event,componentto)=>{
		console.log(componentto);
		this.setState({activepage:componentto});
	}
	isActivated=(componentfor)=>{
		return this.state.activepage === componentfor? this.props.classes.active:'';
	}
	getActivePageComponent=()=>{

	}
	render(){
		const { classes } = this.props;
		const {activepage,currentlocation,geolocation} = this.state;
		const drawer = (
	      <div>
	        <div className={classes.toolbar} />
	       
	        <List component="nav">
	        	<ListItem
				onClick={(event)=>this.changeCurrentPage(event,'NearestMasjid')}
				button
				className={this.isActivated('NearestMasjid')}
				>
				  <ListItemIcon>
					<DraftsIcon />
				  </ListItemIcon>
				  <ListItemText primary="Nearest Masjid" />
				</ListItem>
				<ListItem
				onClick={(event)=>this.changeCurrentPage(event,'PrayerTime')}
				button
				className={this.isActivated('PrayerTime')}
				>
				  <ListItemIcon>
					<InboxIcon />
				  </ListItemIcon>
				  <ListItemText primary="Prayer Time" />
				</ListItem>
			  </List>
	      </div>
	    );
		return(
			<div className={classes.root}>
        <AppBar className={classes.appBar}>
          <Toolbar>
            <IconButton
              color="inherit"
              aria-label="open drawer"
              onClick={this.handleDrawerToggle}
              className={classes.navIconHide}
            >
              <MenuIcon />
            </IconButton>
            <Typography variant="title" color="inherit" noWrap>
              Responsive drawer
            </Typography>
          </Toolbar>
        </AppBar>
        <Hidden mdUp>
          <Drawer
            variant="temporary"
            
            open={this.state.mobileOpen}
            onClose={this.handleDrawerToggle}
            classes={{
              paper: classes.drawerPaper,
            }}
            ModalProps={{
              keepMounted: true, // Better open performance on mobile.
            }}
          >
            {drawer}
          </Drawer>
        </Hidden>
        <Hidden smDown implementation="css">
          <Drawer
            variant="permanent"
            open
            classes={{
              paper: classes.drawerPaper,
            }}
          >
            {drawer}
          </Drawer>
        </Hidden>
        <main className={classes.content}>
          <div className={classes.toolbar} />
          <LocationSearch
		        	defaultValue={currentlocation}
		        	onLocationSearch={this.handleLocationChange}
		        	onGeolocationChange={this.handleGeolocationChange}
		        />
		        {activepage === 'PrayerTime' && 
		        	<PrayerTime
		        		geolocation={geolocation}
		        		currentLocation={currentlocation}
		        	/>
		    	}
		        {activepage === 'NearestMasjid' && 
		        	<NearestMasjid
		        		geolocation={geolocation}
		        		currentLocation={currentlocation}
		        	/>
		    	}
        </main>
      </div>
		)
	}
}
export default withStyles(styles)(PrayerRoot);


